#roles-container {
    position: ABSOLUTE;
    z-index: 2000;
    text-align: center;
    top: 55%;
    left: 50%;
    transform: translate(-50%);
    overflow: hidden;
    background-color: #eeeeee;
    width: 100vw;
    height: 60vh;
}
#buttons-container {
    position: relative;
    top: -4vh;
    width: 100vw;
    height: 37vh;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .role-button {
    position: absolute;
    width: 33vw; /* גודל רחב יותר */
    height: 17vh; /* גובה גבוה יותר */
    color: white;
    text-align: center;
    line-height: 10vh; /* ממרכז את הטקסט בגובה */
    font-family: "Heebo";
    font-weight: 600;
    font-size: 1.2em; /* גודל גופן גדול יותר */
    border: none;
    cursor: pointer;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%); /* צורת משושה */
}

  .role-button:nth-child(1) {
    top: 20%; /* Positioning the first button towards the top */
    left: 50%;
    transform: translateX(-50%);
  }
  
  /* Second button to the left of the center */
  .role-button:nth-child(2) {
    top: 42%; /* Positioning the button below the center one */
    left:8%;
  }
  
  /* Third button to the right of the center */
  .role-button:nth-child(3) {
    top: 42%; /* Same height as the second button */
    right: 8%; /* Move it towards the right side */
  }
  
  /* Fourth button below the second */
  .role-button:nth-child(4) {
    top: 88%; /* Positioning further down */
    left: 8%; /* Left alignment */
  }
  
  /* Fifth button below the third */
  .role-button:nth-child(5) {
    top: 88%; /* Same height as the fourth button */
    right: 8%; /* Right alignment */
  }
  
  /* Sixth button centered below all */
  .role-button:nth-child(6) {
    top: 110%; /* Bottom alignment */
    left: 50%;
    transform: translateX(-50%);
  }
  